import {createRouter, createWebHashHistory} from 'vue-router';

import Moorka from "./components/CarlMoorka.vue"
import discordMoorka from "./components/MyDiscord.vue"
import FibLOX from "./components/FibLoxComporetion.vue"
import TrovoAnia from "./components/TrovoAnia.vue"

export default createRouter({
    history: createWebHashHistory(),
    routes: [
        { path: '/CarlMoorka', component: Moorka, alias:'/'},
        { path: '/DiscordMoorka', component: discordMoorka },
        { path: '/FibLOX', component: FibLOX },
        { path: '/TrovoAnia', component: TrovoAnia },
    ]
})

